<template>
  <div v-if="ModalBoolean" class="modal">
    <div class="modal__block">
      <h2 class="modal__block-title">Отправить картинку</h2>
      <label for="">
        <span>URL</span>
        <input type="text" placeholder="URL" v-model="imgUrl" />
      </label>
      <label for="">
        <span>Комментарии</span>
        <input type="text" placeholder="Комментарии" v-model="comment" />
      </label>
      <div class="modal__block-btns">
        <button @click="closeModal()" class="modal__block-btn close">
          ОТМЕНА
        </button>
        <button class="modal__block-btn send" @click="sendImg(notes)">
          ОТПРАВИТЬ
        </button>
      </div>
    </div>
  </div>
  <div class="footer">
    <input
      class="footer__text"
      type="text"
      placeholder="Написать сообщение..."
      v-model="message"
    />
    <button
      v-if="message.length == 0"
      class="footer__send"
      @click="showModal()"
    >
      <img src="@/assets/images/Vector1.svg" alt="" />
    </button>
    <button v-else class="footer__send" @click="addMess(notes)">
      <img src="@/assets/images/send.svg" alt="" />
    </button>
  </div>
</template>
<script>
export default {
  data() {
    return {
      message: "",
      id: 1,
      ModalBoolean: false,
      imgUrl:'',
      comment: '',
    };
  },
  props: {
    notes:{
      typeof: Array,
    },
    sendId: {
      typeof:Number
    },
    ModalBool:{
      typeof:Boolean
    },
  },
  methods: {
    showModal() {
      this.ModalBoolean = true;
    },
    closeModal() {
      this.ModalBoolean = false;
    },
    addMess(notes) {
      notes.push({
        message: this.message,
        id: this.id++,
        sendId: this.sendId,
        time: this.getTime(),
      });

      this.message = "";
      this.$emit("addMess", notes);
    },
    sendImg(notes) {
      if (this.comment.length != 0 && this.imgUrl != '') {
        notes.push({
          url: this.imgUrl,
          message: this.comment,
          id: this.id++,
          sendId: this.sendId,
          time: this.getTime(),
        });
        this.ModalBoolean = false;
        this.imgUrl = "";
        this.comment = "";
      }
    },
    change() {
      this.ModalBoolean = this.ModalBool;
    },

    getTime() {
      // return `${new Date().getHours()} : ${new Date().getMinutes() } `
      if (new Date().getMinutes() < 10) {
        return `${new Date().getHours()}:0${new Date().getMinutes()} `;
      } else {
        return `${new Date().getHours()} : ${new Date().getMinutes()} `;
      }
    },
  },
  watch:{

  },

};
</script>
<style></style>
